.how-it-works {
    margin-top: 27px;
    padding-bottom: 20px;

    @media (min-width: $tablet-width) {
        margin-top: 32px;
        padding-bottom: 0;
        border-bottom: 1px solid $border-grey-e6;
    }
}

.how-it-works__title {
    margin-bottom: 37px;
    font-size: 36px;
    line-height: 40px;
    font-family: "Oswald", "Arial", sans-serif;
    font-weight: 400;
    color: $basic-black;

    @media (min-width: $tablet-width) {
        padding-top: 10px;
        font-size: 60px;
        line-height: 60px;
        margin: 0;
    }
}

.how-it-works__list {
    margin: 0;
    padding: 0;

    list-style: none;

    @media (min-width: $tablet-width) {
        display: flex;
        flex-wrap: wrap;
        margin-top: 74px;
    }
}

.how-it-works__item {
    margin-bottom: 20px;
    min-height: 60px;

    @media (min-width: $tablet-width) {
        position: relative;
        width: 45%;
        padding-bottom: 113px;

        &:nth-child(even) {  
            margin-left: 65px;
        }
        &:before {
            content: "1";
            display: block;
            position: absolute; 
            top: 96px;
            right: 88px;
            font-size: 280px;
            font-family: "Oswald", "Arial", sans-serif;
            color: $basic-grey;         
        }

        &--second {
            &:before {
               content: "2";
               right: 73px;
            }
        }

        &--third {
            &:before {
               content: "3";
               right: 73px;
            }
        }

        &--fourth {
            &:before {
               content: "4";
               right: 73px;
            }
        }
    }
    
    @media (min-width: $desktop-width) {
       
        width: calc(25% - 60px);
        padding-bottom: 0;
        margin-right: auto;
        margin-bottom: 0;
        min-height: 250px;

        &:nth-child(even) {  
            margin-left: 0;
            margin-right: auto;
        }
        &:nth-child(4n) {
            margin-right: 0;
        }
    } 
}

.how-it-works__description br{
    display: none;

    @media (min-width: $tablet-width) {
        display: inline;
    }
}

.how-it-works__description {
    display: flex;
    padding: 0;
    margin: 0;
   
    &::before {
        content: "";
        display: block;
        margin-right: 20px;
        min-width: 60px;
        min-height: 60px;

        background-color: $basic-green;
        background-image: url("../img/icon_leaf.svg");
        background-repeat: no-repeat;
        background-position: 50% 50%;
        background-size: 32px 30px;

        @media (min-width: $tablet-width) {
            
            width: 80px;
            height: 80px;   
            
        }
    }

    @media (min-width: $tablet-width) {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;

        max-width: 247px;
        min-height: 185px;

        font-size: 16px;
        line-height: 24px;

        z-index: 10;
    }   
}

.how-it-works__item--second .how-it-works__description {
    &:before {
        background-image: url("../img/icon_tub.svg");
        background-size: 29px 31px;
    }
}

.how-it-works__item--third .how-it-works__description {
    &:before {
        background-image: url("../img/icon_eat.svg");
        background-size: 25px 30px;
    }
}

.how-it-works__item--fourth .how-it-works__description {
    &:before {
        background-image: url("../img/icon_alarm.svg");
        background-size: 32px 33px;
    }
}



